.form-style-wrapper {
  // ==========================================================================
  // USPs section
  // ==========================================================================

  .l-usp {
    padding: 2em 0;

    .content {
      height: 100%;
      text-align: center;
    }

    .d-flex {
      flex-direction: column;

      .usp-block {
        width: 90%;
        margin: 0 auto;
        text-align: left;
        display: flex;

        p {
          margin-bottom: 0.3em;
        }

        img {
          margin-right: 8%;
          width: 12%;
        }
      }
    }

    a {
      margin-top: 2em;
    }
  }

  @include media-breakpoint-up(md) {
    .l-usp {
      .d-flex {
        flex-direction: row;

        .usp-block {
          width: 100%;
          margin: 2em auto 0;
          text-align: center;
          display: block;

          img {
            display: block;
            margin: 0 auto;
            width: 100%;
            max-width: 60px;
          }

          p {
            margin: 1em 0.2em;
            font-size: 0.8em;
          }
        }
      }
    }
  }

  @include media-breakpoint-up(lg) {
    .l-usp .d-flex .usp-block p {
      font-size: 1em;
    }
  }
}
