.form-style-wrapper {
  // ==========================================================================
  // Includes styles of all trust elements
  // ==========================================================================

  .trust-start {
    background-color: $grey-bright;

    li {
      align-items: center;
      display: flex;
    }

    i {
      color: $success;
    }
  }

  .trust-closure {
    li {
      i {
        color: $success;
      }

      & > span {
        color: darken($success, 10%);
      }
    }
  }

  @include media-breakpoint-up(md) {
    .trust-start {
      ul {
        grid-template-columns: 1fr 1fr 1fr;
      }

      li {
        align-items: flex-start;
        font-size: 0.85em;
        line-height: 1.4em;
      }
    }
  }
}
