.form-style-wrapper {
  // ==========================================================================
  // Tooltip Classes
  // ==========================================================================

  .tooltip {
    font-size: 0.7em;
    color: $grey-default;
    left: -4px;
    position: relative;
    width: 33px;
    top: -8px;
  }

  .tooltip .tooltiptext {
    visibility: hidden;
    width: 300px;
    background-color: #555;
    font-size: 1rem;
    font-style: normal;
    color: #FFF;
    text-align: center;
    border-radius: 6px;
    padding: 0.75em;
    box-sizing: border-box;
    position: absolute;
    top: 1.75em;
    opacity: 0;
    transition: opacity 0.3s;
    z-index: 110;
    right: 0;
    margin-right: -12px;
    max-width: 55vw;
    line-height: 1.4em;
    font-weight: 400;
  }

  .tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 0;
    right: 20px;
    margin-top: -10px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent #555;
  }

  .tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
  }
}
