.form-style-wrapper {
  // ==========================================================================
  // Hero section overrides for LVZ
  // ==========================================================================

  input[type="submit"].sending {
    background-color: grey;
  }

  input[type="submit"].completed {
    background-color: greenyellow;
  }

  input[type="submit"].failed {
    background-color: orange;
  }

  .l-hero {
    background-color: $white;
    background-position: 0 55px;
    background-repeat: no-repeat;
    background-size: 100vw auto;
    min-height: inherit;

    .nav-bar {
      background: #FFF;
    }

    .content {
      margin-left: auto;
      margin-right: auto;
      max-width: 1024px;

      .logo {
        width: 175px;
        margin-top: 1rem;
      }

      .powered-by {
        width: 70px;
        text-align: left;
        color: $black;
        font-weight: normal;
        justify-content: flex-end;

        img {
          width: 66px;
        }
      }

      nav {
        grid-area: nav;
        text-align: right;
        display: flex;
        flex-direction: row-reverse;

        a {
          color: $black;
        }

        .questions {
          display: none;
        }
      }
    }

    .d-flex {
      flex-wrap: wrap;
    }

    .teaser {
      h1 {
        font-size: 1.5rem;
        line-height: 1.9rem;
        text-align: center;
      }

      .images {
        justify-content: space-between;
        align-items: center;

        .svg {
          width: 22%;
          background: #FFF;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
        }

        .plus {
          width: 10%;
        }

        img {
          width: 27%;
        }
      }
    }

    .trust-start {
      i.tooltip {
        color: $grey-default;
        font-size: 0.88rem;
        top: -4px;
      }
    }

    .wizard {
      background-color: $white;
      margin-left: auto;
      margin-right: auto;
      max-width: 640px;
      padding-top: 2rem;

      h2 {
        display: block;
        width: 95%;
      }

      .package {
        width: 90%;
        margin: 0 auto;
        border: 1px solid $brand-3;
        border-radius: 5px;
        max-width: 330px;

        .h-4 {
          color: $brand-3;
          background: #FFF;
          display: inline;
          position: relative;
          top: -1rem;
          padding: 0 1rem;
        }

        p {
          margin-top: -0.5rem;
          line-height: 1.9rem;
        }

        span {
          color: $brand-3;
          font-weight: bold;
        }
      }

      .radio-image-selector {
        .d-flex {
          justify-content: space-between;

          fieldset {
            width: 100%;
            max-width: 400px;
            margin: 0 auto;
          }
        }

        input[type="radio"]:checked ~ label {
          box-shadow: 0 0 2px 2px #E5007B;
          border: 0;
        }

        label span {
          position: absolute;
          left: 5.5rem;
        }
      }

      .edition {
        .date {
          position: relative;
        }

        .vdp-datepicker__calendar {
          max-width: 100%;
        }

        .vdp-datepicker::after {
          color: $grey-medium;
          content: '\e806';
          font-family: "kecleon-iconfont";
          font-size: 1.5em;
          pointer-events: none;
          position: absolute;
          top: 1px;
          right: 1px;
          border: 0;
          height: 52px;
          width: 49px;
          line-height: 52px;
          margin: 0;
          padding: 0;
          text-align: center;
          cursor: pointer;
          border-left: 1px solid $grey-dark;
          background-color: $white;
          overflow: hidden;
          border-top-right-radius: 4px;
          border-bottom-right-radius: 4px;
        }

        button.icon-streamline-icon-delete-svgomg {
          position: absolute;
          top: 0;
          right: 35px;
          height: 54px;
          width: 38px;
          color: $grey-medium;
          line-height: 5px;
          margin: 0;
          padding: 0;
          text-align: center;
          cursor: pointer;
          background-color: $white;
          border: 1px solid $black;
          font-size: 1.25em;
        }
      }

      .design {
        small .text-muted {
          line-height: 1.6;
        }
      }

      .contact .d-grid {
        grid-template-areas: "title" "fname" "lname" "email" "phone" "company" "button";
        grid-template-columns: 1fr;

        h2 {
          grid-area: title;
        }

        input[name="fname"] {
          grid-area: fname;
        }

        input[name="lname"] {
          grid-area: lname;
        }

        input[name="phone"] {
          grid-area: phone;
        }

        input[name="email"] {
          grid-area: email;
        }

        input[name="company"] {
          grid-area: company;
        }

        button {
          grid-area: button;
        }
      }

      .summary {
        .format-wrapper h5 {
          flex-basis: 100%;
          margin: 1rem 0;
          font-size: 1.2rem;
          border: none;
          text-align: center;
        }

        .format {
          width: 100%;
          padding-bottom: 0.5rem;
          text-align: center;
          -moz-transition: all 100ms ease-in;
          background-color: $white;
          border: 1px solid $grey-light;
          border-radius: 4px;
          line-height: 1rem;

          &.d-flex {
            align-items: center;
            max-width: 280px;
            margin: 0 auto 0.5em;
          }

          &.m-50-percent img {
            margin-top: 50%;
          }

          img {
            margin: 0.5em 1.5em 0;
            width: 20%;
          }

          strong {
            line-height: 2rem;
          }
        }

        h5 {
          border-bottom: 1px solid $grey-default;
          position: relative;
        }

        .icon-streamline-icon-pencil-2-svgomg {
          color: $grey-default;
          position: absolute;
          right: 0;
          font-size: 0.8em;
        }
      }

      .closure {
        background-color: $grey-bright;

        .privacy {
          a {
            color: inherit;
            text-decoration: underline;
          }
        }

        button {
          max-width: inherit;
        }

        .notification {
          &.completed {
            background-color: $success;
          }

          &.error {
            background-color: $attention;
          }

          p {
            opacity: 0.7;
          }
        }

        .text-muted {
          line-height: 1.1em;
        }
      }

      .price {
        background-color: rgba($brand-3, 0.15);

        .d-grid.price-input {
          grid-template-areas: "title" "proposedPrice-title" "proposedPrice-field" "salePrice-title" "salePrice-field" "wunschpreis-title" "wunschpreis-field" "savingPrice-field";
          grid-template-columns: 1fr;
          align-items: center;
          grid-gap: 0.75em;

          h2 {
            grid-area: title;
          }

          p {
            margin: 0;
          }

          .proposedPrice-title {
            grid-area: proposedPrice-title;
          }

          .proposedPrice-field {
            grid-area: proposedPrice-field;
            background: $grey-bright;
            border: 1px solid $grey-light;
            border-radius: 4px;
            color: $grey-default;
            padding: 1em;
          }

          .salePrice-title {
            grid-area: salePrice-title;

            small {
              color: #E5007B;
              font-weight: bold;
            }
          }

          .salePrice-field {
            grid-area: salePrice-field;
            background: $grey-bright;
            border: 1px solid $grey-light;
            border-radius: 4px;
            color: $grey-default;
            padding: 1em;
          }

          .wunschpreis-title {
            grid-area: wunschpreis-title;
          }

          .wunschpreis-field {
            box-shadow: 0 0 2px 2px #E5007B;
            grid-area: wunschpreis-field;
            display: flex;
            border: 1px solid #E5007B;
            border-radius: 4px;
            overflow: hidden;

            input {
              width: 90%;
              border: none;
              border-bottom-right-radius: 0;
              border-top-right-radius: 0;
            }

            .euro {
              width: 10%;
              background: $grey-bright;
              color: $black;
              padding-top: 0.5em;
              box-sizing: border-box;
              text-align: center;
              line-height: 2.25;
            }
          }

          .savingPrice-field {
            color: $white;
            grid-area: savingPrice-field;
            background-color: $success;
            border: 1px solid $success;
            border-radius: 4px;
            padding: 0.5em;
            text-align: center;
          }

          button {
            grid-area: button;
          }
        }
      }
    }
  }

  @include media-breakpoint-up(sm) {
    .wizard {
      box-shadow: 0 0 4px 4px rgba($black, 0.2);
      border: 1px solid #FFF;
      border-radius: 5px;
    }

    .l-hero .wizard .price .d-grid.price-input {
      grid-template-areas: "title title" "proposedPrice-title proposedPrice-field" "salePrice-title salePrice-field" "wunschpreis-title wunschpreis-field" "savingPrice-field savingPrice-field";
      grid-template-columns: 0.5fr 1fr;
      grid-gap: 1em;
    }

    .l-hero .content .logo {
      margin: 0.75rem 0 0;
    }

    .l-hero .teaser {
      padding-top: 1rem;
      padding-bottom: 1rem;

      h1 {
        font-size: 2rem;
        line-height: 2.3rem;
      }
    }

    .l-hero .wizard {
      .package {
        max-width: 400px;
        padding: 0 1rem;

        p {
          font-size: 1.3em;
          line-height: 2.4rem;
        }
      }

      .format-wrapper {
        justify-content: space-between;
      }

      .summary .format {
        width: 31%;

        &.d-flex {
          justify-content: space-evenly;
        }

        img {
          width: 50%;
        }
      }

      .edition button.icon-streamline-icon-delete-svgomg {
        width: 48px;
        right: 126px;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .l-hero {
      background-position: center top;

      .nav-bar .content {
        max-width: 690px;
        padding: 0 1rem;
      }

      .content {
        grid-template-columns: 120px auto auto;

        .powered-by {
          small {
            line-height: 1.4rem;
          }
        }
      }

      .content nav {
        padding-top: 0.5rem;
      }

      .content nav .questions {
        display: block;
        margin-right: 1rem;
        font-size: 0.8rem;
        line-height: 1.2rem;

        span {
          text-decoration: underline;
        }
      }

      .content .logo {
        width: 200px;
        margin: 1rem 0 0;
      }

      .teaser {
        margin-top: 0;
        margin-bottom: -8rem;
        max-width: 800px;

        h1 {
          width: 50%;
          box-sizing: border-box;
          font-size: 2.5rem;
          line-height: 3rem;
          text-align: left;
          color: #000;
        }

        .images {
          width: 50%;
          padding-top: 3rem;
          box-sizing: border-box;

          img {
            position: relative;
          }

          .svg-1 {
            width: 39%;
            z-index: 2;
          }

          .plus-1 {
            right: 12%;
            width: 20%;
            height: 20%;
            z-index: 3;
          }

          .svg-2 {
            width: 39%;
            right: 36%;
            top: -9%;
            z-index: 1;
          }

          .plus-2 {
            right: -56%;
            width: 15%;
            height: 15%;
            z-index: 3;
            top: -63%;
          }

          .svg-3 {
            width: 50%;
            top: -59%;
            left: -7%;
          }
        }
      }

      .wizard {
        .contact .d-grid {
          grid-gap: 0.5em 1em;
          grid-template-areas: "title title" "fname lname" "email phone" "company company" "button button";
          grid-template-columns: 1fr 1fr;
        }

        .radio-image-selector .d-flex fieldset {
          width: 31%;
        }

        .radio-image-selector label span {
          position: relative;
          left: 0;
        }
      }
    }
  }

  @include media-breakpoint-up(lg) {
    .l-hero .nav-bar .content {
      max-width: 700px;
    }

    .l-hero .teaser {
      max-width: 1024px;
      justify-content: center;

      h1 {
        width: 40%;
        box-sizing: border-box;
        font-size: 2.5rem;
        line-height: 3rem;
        margin-left: 2rem;
      }

      .images {
        width: 34%;
      }
    }
  }

  @include media-breakpoint-up(xxl) {
    .l-hero {
      .nav-bar .content {
        max-width: 1024px;
      }

      .content .logo {
        width: 300px;
      }

      .teaser {
        margin-top: 2rem;

        h1 {
          width: 46%;
          font-size: 3rem;
          line-height: 4rem;
        }

        .images {
          width: 50%;
        }
      }

      .wizard {
        margin-top: -10rem;
      }
    }
  }

  .plus-in-span {
    font-weight: bold;
    color: $brand-3;
  }
}
