.form-style-wrapper {
  // ==========================================================================
  // Text Only section
  // ==========================================================================

  .l-text-only {
    background-color: $grey-bright;
    padding: 2em 0;

    .content {
      max-width: 600px;
      margin: 0 auto;
      padding: 0 1em;

      a {
        margin-top: 1em;
      }
    }

    .intro {
      margin: 1em 0;
    }
  }

  @include media-breakpoint-up(md) {
    .l-text-only {
      .content {
        padding: 0 4em;
      }
    }
  }
}
