.form-style-wrapper {
  // ==========================================================================
  // Hero section
  // ==========================================================================

  .l-hero {
    background-color: $grey-light;
    // background-image: url('/images/grassy-field.jpg');
    background-size: cover;
    background-position: fixed;
    min-height: 80vh;

    .content {
      height: 100%;
      text-align: center;
    }

    .intro,
    .visual {
      margin: 2em;
    }

    .wizard {
      .dropdown {
        margin: 1em 0;
        padding: 1em;
        position: relative;
        max-height: 70px;
        overflow: hidden;
        transition: all 0.3s;
        border-top: 1px solid #BBB;
        border-bottom: 1px solid #BBB;
        cursor: pointer;

        .summary-link {
          position: absolute;
          display: none;
          bottom: 0;
          left: 0;
          width: 100%;
          text-align: center;
          margin: 0;
          padding: 30px 0;
          background-image: linear-gradient(to bottom, transparent, #F6F6F6);

          span {
            background: #FFF;
            padding: 1rem;
            border-radius: 5px;
            border: 1px solid #FFF;
            box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.2);
            cursor: pointer;
          }
        }

        i.icon-streamline-icon-arrow-button-down-svgomg {
          position: absolute;
          right: 0.1em;
          top: 1.4rem;
          transition: all 0.3s;
          color: $brand-3;
          font-size: 1.3rem;
        }
      }

      .dropdown.show {
        max-height: 1100px;
        transition: all 1s;

        i.icon-streamline-icon-arrow-button-down-svgomg {
          transform: rotate(180deg);
          transition: all 0.3s;
        }

        .summary-link {
          display: none;
        }
      }
    }
  }
}
